import React from 'react';
import { Form, Input, Button } from 'antd';
import { useFormik } from 'formik';
import pathname from '../../../constants';
import { validationSchema } from '../constants';

function OrderEditForm({ edit: { data, editDataAction }, setShowModal }) {
  const {
    handleSubmit, values, errors, touched, handleChange, handleBlur,
  } = useFormik({
    initialValues: {
      email: data.email,
      phone: data.phone,
      deliveryTime: data.deliveryTime,
    },
    onSubmit: (val) => {
      editDataAction({ name: pathname.ORDER, data: val, id: data.id });
      setShowModal(false);
    },
    validationSchema,
  });

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Item
        validateStatus={touched.email && errors.email && 'error'}
        help={touched.email && errors.email}
        label="Email"
      >
        <Input
          name="email"
          placeholder="email"
          value={values.email}
          onBlur={handleBlur}
          onChange={handleChange}
        />
      </Form.Item>
      <Form.Item
        validateStatus={touched.phone && errors.phone && 'error'}
        help={touched.phone && errors.phone}
        label="phone"
      >
        <Input
          name="phone"
          placeholder="phone"
          value={values.phone}
          onBlur={handleBlur}
          onChange={handleChange}
        />
      </Form.Item>
      <Form.Item
        validateStatus={touched.deliveryTime && errors.deliveryTime && 'error'}
        help={touched.deliveryTime && errors.deliveryTime}
        label="Delivery Time"
      >
        <Input
          name="deliveryTime"
          placeholder="delivery Time"
          value={values.deliveryTime}
          onBlur={handleBlur}
          onChange={handleChange}
        />
      </Form.Item>
      <Form.Item>
        <Button type="primary" htmlType="submit">Save</Button>
        <Button style={{ marginLeft: '15px' }} type="primary" onClick={() => setShowModal(false)}>Cancel</Button>
      </Form.Item>
    </Form>
  );
}

export default OrderEditForm;
