import React, { useEffect, useRef, useState } from 'react';
import { Avatar, Icon, Button } from 'antd';
import { useDispatch } from 'react-redux';
import ReactToPrint from 'react-to-print';
import pathname from '../../constants';
import { downloadOrderByIds } from '../../store/order-download/action';
import { editFetching } from '../../store/edit/action';
import {delTimeRow, renderContent} from './constants';

const PrintIcon = ({ onClick, setData }) => {
  const handleClick = () => {
    setData();
    setTimeout(() => {
      onClick();
    }, 10);
  };

  return <span onClick={handleClick} role="presentation"><Icon type="printer" theme="outlined" /></span>;
};

export default function useClients({ showAcceptConfirm, showDeleteConfirm, componentRef }, dependencies = []) {
  const [expanded, setExpanded] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [printData, setPrintData] = useState(null);
  const dispatch = useDispatch();

  // eslint-disable-next-line
  useEffect(() => setExpanded([]), dependencies);

  function setExpandedKeys(key) {
    const keyIndex = expanded.indexOf(key);
    if (keyIndex === -1) {
      setExpanded([...expanded, key]);
    } else {
      const newExpanded = [...expanded];
      newExpanded.splice(keyIndex, 1);
      setExpanded(newExpanded);
    }
  }


  const columns = [
    {
      title: 'ID',
      dataIndex: 'orderId',
      render: renderContent,
    },
    {
      dataIndex: 'url',
      render: (url, rest) => {
        return (url && (
        <Avatar
          size={60}
          src={`${process.env.REACT_APP_HOST_NAME}/products/${rest.productId}/${url}`}
        />
        ));
      },
    },
    {
      title: 'Ապրանքի անուն',
      dataIndex: 'productName',
    },
    {
      title: 'Քանակ',
      dataIndex: 'count',
    },
    {
      title: 'Գին',
      dataIndex: 'price',
    },
    {
      title: 'Շտրիխ կոդ',
      dataIndex: 'xmlBarCode',
    },
    {
      title: 'Ընդ․ գին',
      dataIndex: 'totalWithOutPromo',
      render: renderContent,
    },
    {
      title: 'Զեղչ',
      dataIndex: 'totalWithPromo',
      render: renderContent,
    },
    {
      title: 'Ընդ. գումար',
      dataIndex: 'totalAmount',
      render: renderContent,
    },
    {
      title: 'Վճ․ եղանակ',
      //className: 'order-table-max-width',
      dataIndex: 'paymentType',
      render: renderContent,
    },
    // {
    //   title: 'Անուն ազգանուն',
    //   className: 'order-table-max-width',
    //   dataIndex: 'userName',
    //   render: renderContent,
    // },

    {
      title: 'հեռ․',
      dataIndex: 'phone',
      render: renderContent,
    },
    {
      title: 'Առաքման հասցե',
      dataIndex: 'deliveryAddress',
      render: renderContent,
    },
    {
      title: 'Ժամանակահատված',
      dataIndex: 'deliveryTime',
      render: renderContent,

    },

    {
      dataIndex: 'lastKey',
      render: (lastKey, row) => {
        // eslint-disable-next-line
        const value = (<a onClick={() => setExpandedKeys(lastKey)}>Ավելին</a>);
        return renderContent(value, row);
      },
    },
    {
      render: (_, row) => {
        const { orderId } = row;
        const value = (
          <Button
            onClick={() => showAcceptConfirm(orderId)}
            type="primary"
          >
            Հաստատել
          </Button>
        );
        return renderContent(value, row);
      },
    },
    {
      render: (row) => {
        const value = (
          <div className="toDowload">
            <Icon
              type="download"
              onClick={() => {
                const ids = [row.orderId];
                dispatch(downloadOrderByIds({ ids }));
              }}
            />
          </div>
        );
        return renderContent(value, row);
      },
    },
    {
      render: (row) => {
        const value = (
          <div className="toPrint">
            <ReactToPrint
              trigger={() => <PrintIcon setData={() => setPrintData(row)} />}
              content={() => componentRef.current}
            />
          </div>
        );
        return renderContent(value, row);
      },
    },
    {
      dataIndex: 'edit',
      render: (_, row) => {
        const { orderId } = row;
        const value = (
          <div className="toEdit">
            <Icon
              onClick={() => {
                dispatch(editFetching({ name: pathname.ORDER, id: orderId }));
                setShowModal(true);
              }}
              type="edit"
            />
          </div>
        );
        return renderContent(value, row);
      },
    },
    {
      render: (_, row) => {
        const { orderId } = row;
        const value = (
          <div className="toDelete">
            <Icon type="delete" onClick={() => showDeleteConfirm(orderId)} />
          </div>
        );
        return renderContent(value, row);
      },
    },
  ];
  return {
    columns, expanded, showModal, setShowModal, printData,
  };
}
