import React, { useState } from 'react';
import {
  Checkbox, Row, Col, Button,
} from 'antd';
import './orderDownloadForm.css';
import { useDispatch, useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import { setDownloadSettings } from '../../../../store/order-download/action';


function OrderDownloadForm({ setShowModal }) {
  const dispatch = useDispatch();
  const initSettings = useSelector((state) => state.orderDownload.downloadSettings);
  const [orderInfo, setOrderInfo] = useState(initSettings.orderInfo);
  const [orderedProdsInfo, setOrderedProdsInfo] = useState(initSettings.orderedProdsInfo);
  const disabled = !(orderInfo.length || orderedProdsInfo.length);
  return (
    <div className="check">
      <Checkbox.Group
        style={{ width: '100%' }}
        defaultValue={orderedProdsInfo}
        onChange={(val) => {
          setOrderedProdsInfo(val);
        }}
      >
        <Row>
          <Col span={12}>
            <div className="leftCheckBox">
              <p>Ապրանքի անունը</p>
              <Checkbox value="productName" />
            </div>
          </Col>
          <Col span={12}>
            <div className="rightCheckBox">
              <Checkbox value="count" />
              <p>Ապրանքի քանակը</p>
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <div className="leftCheckBox">
              <p>Շտրիխ կոդ</p>
              <Checkbox value="xmlBarCode" />
            </div>
          </Col>
          <Col span={12}>
            <div className="rightCheckBox">
              <Checkbox value="price" />
              <p>1 միավորի գինը</p>
            </div>
          </Col>

        </Row>
      </Checkbox.Group>
      <Checkbox.Group
        style={{ width: '100%' }}
        defaultValue={orderInfo}
        onChange={(val) => {
          setOrderInfo(val);
        }}
      >
        <Row>
          <Col span={12}>
            <div className="leftCheckBox">
              <p>Գումար (ներառյալ առաքում)</p>
              <Checkbox value="totalAmount" />
            </div>
          </Col>
          <Col span={12}>
            <div className="rightCheckBox">
              <Checkbox value="firstName" />
              <p>Անուն ազգանուն</p>
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <div className="leftCheckBox">
              <p>Հեռ․</p>
              <Checkbox value="phone" />
            </div>
          </Col>
          <Col span={12}>
            <div className="rightCheckBox">
              <Checkbox value="email" />
              <p>Էլ․հասցե</p>
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <div className="leftCheckBox">
              <p>Ընդհանուր գին</p>
              <Checkbox value="totalWithOutPromo" />
            </div>
          </Col>
          <Col span={12}>
            <div className="rightCheckBox">
              <Checkbox value="deliveryPrice" />
              <p>Առաքման գումար</p>
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <div className="leftCheckBox">
              <p>Զեղզված գին</p>
              <Checkbox value="totalWithPromo" />
            </div>
          </Col>
          <Col span={12}>
            <div className="rightCheckBox">
              <Checkbox value="deliveryTime" />
              <p>Առաքման ժամանակահատված</p>
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <div className="leftCheckBox">
              <p>Կուտակված բալեր</p>
              <Checkbox value="bonus" />
            </div>
          </Col>
          <Col span={12}>
            <div className="rightCheckBox">
              <Checkbox value="createdAt" />
              <p>Պատվերի հաստատման ամսաթիվ</p>
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <div className="leftCheckBox">
              <p>Առաքման հասցե</p>
              <Checkbox value="deliveryAddress" />
            </div>
          </Col>
          <Col span={12}>
            <div className="rightCheckBox">
              <Checkbox value="paymentType" />
              <p>Վճարման եղանակ</p>
            </div>
          </Col>
        </Row>
      </Checkbox.Group>

      <div className="orderBtns">
        <Button
          type="primary"
          disabled={disabled}
          onClick={() => {
            dispatch(setDownloadSettings({ orderInfo, orderedProdsInfo }));
            setShowModal(false);
          }}
        >
        Save
        </Button>
        <Button className="cancel" onClick={() => setShowModal(false)}>Cancel</Button>
      </div>
    </div>
  );
}

OrderDownloadForm.propTypes = {
  setShowModal: PropTypes.func.isRequired,
};

export default OrderDownloadForm;
