import React, { useState, memo, useRef } from 'react';
import { Table, Modal, Button } from 'antd';
import useClients from '../useClients';
import OrderEditForm from './OrderEditForm';
import OrderDownloadForm from './order-download-form/OrderDownloadForm';
import { columnsDropCurrent } from '../constants';
import ComponentToPrint from './print-component/PrintComponent';


function CurrentOrder({
  dataSource, edit, loading, order,
}) {
  const { editFetchingResetAction } = edit;
  const { acceptOrderAction, deleteOrderAction } = order;
  const [downloadModal, setDownloadModal] = useState(false);
  const { confirm } = Modal;

  const componentRef = useRef();
  const showAcceptConfirm = (orderId) => confirm({
    content: 'Confirm order',
    okText: 'Yes',
    okType: 'primary',
    cancelText: 'No',
    onOk() {
      acceptOrderAction({ orderId });
    },
    onCancel() {
    },
  });
  const showDeleteConfirm = (orderId) => confirm({
    content: 'Delete order?',
    okText: 'Yes',
    okType: 'primary',
    cancelText: 'No',
    onOk() {
      deleteOrderAction({ orderId });
    },
    onCancel() {
    },
  });

  const {
    columns, expanded, showModal, setShowModal, printData,
  } = useClients({ showAcceptConfirm, showDeleteConfirm, componentRef }, [dataSource]);

  return (
    <div>
      <Button className="download" type="primary" onClick={() => setDownloadModal(true)}>Download Settings</Button>
      <Table
        columns={columns}
        expandedRowRender={(dataDrop) => (
          <Table columns={columnsDropCurrent} dataSource={[dataDrop]} pagination={false} />
        )}
        dataSource={dataSource}
        expandedRowKeys={expanded}
        expandIcon={() => null}
        pagination={false}
        loading={loading}
        scroll={{ x: true }}
      />
      <Modal
        visible={showModal}
        onCancel={() => setShowModal(false)}
        title="Edit"
        footer={null}
        destroyOnClose
        afterClose={editFetchingResetAction}
      >
        {!edit.data ? '...Loading' : (
          <OrderEditForm edit={edit} setShowModal={setShowModal} />
        )}
      </Modal>
      <Modal
        visible={downloadModal}
        onCancel={() => setDownloadModal(false)}
        className="download-attributes"
        title="Download"
        footer={null}
        destroyOnClose
      >
        <OrderDownloadForm setShowModal={setDownloadModal} />
      </Modal>


      <div style={{ display: 'none' }}>
        <ComponentToPrint ref={componentRef} printData={printData} />

      </div>
    </div>
  );
}

export default memo(CurrentOrder);
