import React from 'react';
import { Table } from 'antd';
import PropTypes from 'prop-types';
import { columnsDrop } from '../constants';
import useExpanded from '../useExpanded';


function Archive({ dataSource, loading, deleteOrderAction }) {
  const { archiveColumns, expanded } = useExpanded([dataSource],deleteOrderAction);

  return (
    <div>
      <Table
        columns={archiveColumns}
        dataSource={dataSource}
        pagination={false}
        loading={loading}
        scroll={{ x: true }}
        expandedRowRender={(dataDrop) => (
          <Table columns={columnsDrop} dataSource={[dataDrop]} pagination={false} />
        )}
        expandedRowKeys={expanded}
      />
    </div>
  );
}

Archive.defaultProps = {
  dataSource: [],
  loading: false,
};

Archive.propTypes = {
  dataSource: PropTypes.arrayOf(PropTypes.object),
  loading: PropTypes.bool,
};

export default Archive;
