import React, { useCallback, useEffect, useState } from 'react';
import { Pagination, Tabs } from 'antd';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import CurrentOrder from './CurrentOrder';
import Archive from './Archive';
import { getTableData, getTableDataReset, setKey } from '../../../store/tables/action';
import {
  editData, editFetching, editFetchingReset,
} from '../../../store/edit/action';
import { acceptOrder, deleteOrder } from '../../../store/order/action';
import pathname from '../../../constants';
import { searchReset } from '../../../store/search/action';

const { TabPane } = Tabs;
const name = pathname.ORDER;


function OrderTab({
  list,
  page,
  pages,
  isFetching,
  getTableDataAction,
  edit,
  editFetchingAction,
  editFetchingResetAction,
  editDataAction,
  deleteOrderAction,
  order,
  acceptOrderAction,
  orderKey,
  setKeyAction,
  getTableDataResetAction,
  searchResetAction,
}) {
  const [currentPage, setCurrentPage] = useState({
    currentOrder: 1,
    archiveOrder: 1,
  });
  useEffect(() => {
    getTableDataAction({ name, page: currentPage[orderKey] });
  }, [getTableDataAction, orderKey, currentPage]);
  useEffect(() => () => {
    searchResetAction();
    getTableDataResetAction();
  }, [searchResetAction, getTableDataResetAction]);

  const handleTabChange = useCallback((tabKey) => {
    getTableDataResetAction();
    setKeyAction({ key: tabKey });
  },
  // eslint-disable-next-line
    [setKeyAction]);
  const changePage = useCallback(
    (newPage) => {
      getTableDataAction({ name, page: newPage });
      setCurrentPage({ ...currentPage, [orderKey]: newPage });
    },
    [getTableDataAction, orderKey, currentPage],
  );
  return (
    <div>
      <Tabs onChange={handleTabChange} defaultActiveKey="currentOrder">
        <TabPane tab="Ընթացիկ պատվերներ" key="currentOrder">
          <CurrentOrder
            dataSource={list}
            loading={isFetching}
            edit={{
              ...edit,
              editFetchingAction,
              editFetchingResetAction,
              editDataAction,
            }}
            order={{ ...order, acceptOrderAction, deleteOrderAction }}
          />
          <Pagination onChange={changePage} current={page} total={pages * 10} />
        </TabPane>
        <TabPane tab="Արխիվ" key="archiveOrder">
          <Archive
            dataSource={list}
            loading={isFetching}
            deleteOrderAction={deleteOrderAction}
          />
          <Pagination onChange={changePage} current={page} total={pages * 10} />
        </TabPane>
      </Tabs>
    </div>
  );
}

OrderTab.propTypes = {
  list: PropTypes.arrayOf(Array).isRequired,
  isFetching: PropTypes.bool.isRequired,
  getTableDataAction: PropTypes.func.isRequired,
  pages: PropTypes.number.isRequired,
  page: PropTypes.number.isRequired,
  edit: PropTypes.objectOf(Object).isRequired,
  editFetchingAction: PropTypes.func.isRequired,
  editFetchingResetAction: PropTypes.func.isRequired,
  editDataAction: PropTypes.func.isRequired,
  acceptOrderAction: PropTypes.func.isRequired,
  deleteOrderAction: PropTypes.func.isRequired,
  order: PropTypes.objectOf(Object).isRequired,
  orderKey: PropTypes.string.isRequired,
  setKeyAction: PropTypes.func.isRequired,
  getTableDataResetAction: PropTypes.func.isRequired,
  searchResetAction: PropTypes.func.isRequired,
};

const mapStateToProps = ({
  tables,
  edit,
  order,
}) => {
  const {
    list,
    isFetching,
    pages,
    page,
    key,
  } = tables;
  return ({
    list,
    isFetching,
    pages,
    page,
    orderKey: key,
    edit,
    order,
  });
};
export default connect(mapStateToProps, {
  getTableDataAction: getTableData,
  editFetchingAction: editFetching,
  editFetchingResetAction: editFetchingReset,
  editDataAction: editData,
  deleteOrderAction: deleteOrder,
  acceptOrderAction: acceptOrder,
  setKeyAction: setKey,
  getTableDataResetAction: getTableDataReset,
  searchResetAction: searchReset,
})(OrderTab);
