import React, { useEffect, useState } from 'react';
import { Icon } from 'antd';
import { renderContent } from './constants';
import confirm from "antd/lib/modal/confirm";

export default function useExpanded(dependencyArr = [], deleteOrderAction) {
  const [expanded, setExpanded] = useState([]);

  useEffect(
    () => setExpanded([]),
    // eslint-disable-next-line
    dependencyArr,
  );

  function setExpandedKeys(key) {
    const keyIndex = expanded.indexOf(key);
    if (keyIndex === -1) {
      setExpanded([...expanded, key]);
    } else {
      const newExpanded = [...expanded];
      newExpanded.splice(keyIndex, 1);
      setExpanded(newExpanded);
    }
  }
  const archiveColumns = [
    {
      title: 'ID',
      dataIndex: 'orderId',
      render: renderContent,
    },
    {
      title: 'Ապրանքի անուն',
      dataIndex: 'productName',
    },
    {
      title: 'Քանակ',
      dataIndex: 'count',
    },
    {
      title: '1 միավորի գին',
      dataIndex: 'price',
    },
    {
      title: 'Շտրիխ կոդ',
      dataIndex: 'xmlBarCode',
    },
    {
      title: 'Ընդհանուր գին',
      dataIndex: 'totalWithOutPromo',
      render: renderContent,
    },
    {
      title: 'Զեղչված գին',
      dataIndex: 'totalWithPromo',
      render: renderContent,
    },
    // {
    //   title: 'Պրոմոկոդ',
    //   dataIndex: 'promoCode',
    //   render: renderContent,
    // },
    // {
    //   title: 'Բալ',
    //   dataIndex: 'bonus',
    //   render: renderContent,
    // },
    {
      title: 'Անուն Ազգանուն',
      className: 'order-table-max-width',
      dataIndex: 'userName',
      render: renderContent,
    },
    {
      title: 'հեռ․',
      dataIndex: 'phone',
      render: renderContent,
    },
    {
      title: 'Էլ․հասցե',
      className: 'order-table-max-width',
      dataIndex: 'email',
      render: renderContent,
    },
    {
      title: 'Ստացվել է',
      dataIndex: 'createdAt',
      render:(_,row)=>{

        return ({ children:row?.createData?.slice?.(0,row.createData.length-3), props: {
            rowSpan: row.rowSpan,
          }})

      }
    },
    {
      dataIndex: 'lastKey',
      render: (lastKey, row) => {
        // eslint-disable-next-line
        const value = (<a onClick={() => setExpandedKeys(lastKey)}>Ավելին</a>);
        return renderContent(value, row);
      },
    },

    {
      dataIndex: 'delete',
      render: (lastKey, row) => {
        // eslint-disable-next-line
        // const value = (<button onClick={() => deleteOrderAction({orderId:row.orderId,archive: true})}>Ջնջել</button>);
        const value = (
          <div className="toDelete"  onClick={()=> {

            confirm({
                  onOk: () => {
                    deleteOrderAction({ orderId: row.orderId, archive: true })
                    return false
                  },
                  content: <div style={{paddingLeft: '38px'}}>
                    Համոզված եք որ ուզում եք ջնջել  պատվերը</div>,
                  onCancel:()=>false
                }
            )}
          }>
            <span >
              <Icon type="delete" />
            </span>

          </div>
        );


        return renderContent(value, row);
      },
    },

    renderContent,
  ];

  return {
    archiveColumns, expanded,
  };
}
